.p-contact {
  padding: 100px 20px 40px;
  background-color: $base_white_light;
  @include media(lg) {
    max-width: 1000px;
    margin: 0 auto;
  }
}
.p-contact__form {
  @include media(lg) {
    max-width: 700px;
    margin: 0 auto;
  }
}

.p-contact__item {
  width: 100%;
  margin-bottom: $mg_bottom_md;

  & textarea {
    width: 100%;
    height: 100px;
  }
}

.p-contact__label {
  display: block;
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.p-contact__input {
  display: block;
  width: 100%;
  padding: 5px;
  @include media(lg) {
    padding: 10px;
  }

  &.select {
    max-width: 200px;
  }
}

.p-contact__must {
  @include font-size-rem(12);
  background-color: $base_green;
  border-radius: 2px;
  color: rgb(255, 255, 255);
  margin-left: 5px;
  padding: 2px 6px;
  font-weight: bold;
  letter-spacing: 2px;
}

.p-contact__submit {
  border-radius: 4px;
  border: 1px solid;
  display: block;
  margin: 0 auto;
  padding: 8px 0;
  width: 100%;
  max-width: 350px;
  background-color: $base_gray_light;
}